
.view{
    width: 950px;
    margin: 0 auto;
    padding: 80px 0 15px;
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9;
    height: 45px;
    padding-top: 6px;
    color: rgba(255, 255, 255, 0.75);
    background-color: #24292e;
}
.container {
    width: 980px;
    padding: 0 15px;
    margin: 0 auto;
}
.logo {
    float: left;
    width: 35px;
    height: 35px;
    margin: -1px 15px -1px -2px;
    color: #fff;
    background-size: 100%;
    white-space: nowrap;
    border-radius: 50%;
}
.profile{
  position: relative;
  float: right;
  height: 33px;
  padding: 3px;
}
.profile-img{
  height: 100%;
}
.profile-content{
    display: none;
    position: absolute;
    top: 41px;
    right: -15px;
    z-index: 9;
    width: 160px;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(27,31,35,0.2);
    border-radius: 4px;
    box-shadow: 0 3px 12px rgba(27,31,35,0.15);
}
.profile:hover .profile-content{
    display: block;
}
.profile-content::before,
.profile-content::after{
    content: '';
    position: absolute;
    right: 20px;
    width: 0;
    height: 0;
    border-width: 0 8px 7px;
    border-style: solid;
}
.profile-content::before{
  right: 20px;
  bottom: 100%;
  border-color: #aaa transparent;
}
.profile-content::after{
  right: 20px;
  bottom: calc(100% - 1px);
  border-color: #fff transparent;
}
.profile-item{
  padding: 5px 10px;
  font-size: 14px;
  color: #555;
}
.profile-item:first-child{
  margin-bottom: 5px;
  border-bottom: 1px solid #ccc;
}
.profile-item-title{
  display: block;
  margin-bottom: 6px;
  font-size: 14.5px;
  color: #222;
}
.nav .nav-li {
    float: left;
    position: relative;
}
.nav .nav-a {
    position: relative;
    display: block;
    padding: 0 12px;
    line-height: 32px;
    color: #ccc;
    text-decoration: none;
}
.nav .nav-a:hover {
    color: #f5f5f5;
}
.nav .nav-a.active{
    color: #fff;
}
.nav .nav-a:hover::before {
    display: none;
    position: absolute;
    left: 15px;
    bottom: -7px;
    content: '';
    width: 0;
    height: 0;
    border-width: 0 7px 6px;
    border-color: #f5f5f5 transparent;
    border-style: solid;
}
.nav .nav-a i {
    font-size: 14px;
}
.nav .nav-li-detail{
    display: none;
    position: absolute;
    top: 40px;
    width: 200px;
    padding: 10px;
    background-color: #f5f5f5;
}
/* .nav .nav-a .down {
    margin-left: -4px;
    font-size: 12px;
} */
.nav .nav-a .nav-text {
    font-size: 15px;
}
.nav .search {
    margin-top: 2px;
    margin-left: 30px;
    background-color: rgba(255, 255, 255, .15);
    border-radius: 15px;
    border: none;
    height: 30px;
    padding: 0 30px 0 10px;
    line-height: 20px;
    font-size: 14px;
    color: #fff;
    width: 190px;
    outline: none;
}
.nav .search-icon {
    position: absolute;
    top: 6px;
    right: 0;
    width: 30px;
    cursor: pointer;
}
.nav .search-icon:hover {
    color: #f1f1f1;
}
.main{
    width: 960px;
}



